<script setup>
document.title = '小技资源网';
import {reactive} from "vue";
import {useLoadingBar, useMessage} from "naive-ui";
import {homeGetResource} from "@/assets/js/api";
import {FolderOutlined, TagOutlined} from '@vicons/antd'
import {useRouter} from "vue-router";
import useClipboard from 'vue-clipboard3'

const loadingBar = useLoadingBar()
const router = useRouter()
const message = useMessage()
const {toClipboard} = useClipboard()

const queryData = reactive({
  kwd: '',
  order: 'desc',
  orderby: 'update_time',
})

const data = reactive({
  resourceData: [],
  loading: false,
})


const getResourceData = () => {
  data.loading = true
  loadingBar.start()
  homeGetResource(queryData).then(res => {
    if (res.data.code === 200) {
      data.resourceData = res.data.data
      data.loading = false
      loadingBar.finish()
    } else if (res.data.code === 400) {
      message.error(res.data.msg)
      data.loading = false
      loadingBar.error()
    } else if (res.data.code === 401) {
      message.warning(res.data.msg)
      data.loading = false
      loadingBar.error()
    }
  }).catch(err => {
    message.error(err.message)
    data.loading = false
    loadingBar.error()
  })
}
getResourceData()

const linkTo = (id) => {
  let newUrl = router.resolve({
    name: 'resource',
    query: {
      resourceId: id
    }
  })
  window.open(newUrl.href, "_blank");
}
const openUrl = (url) => {
  window.open(url, "_blank");
}
const copy = async (text) => {
  if (navigator.clipboard) {
    await navigator.clipboard.writeText(text).then(() => {
      message.success('复制成功')
    }).catch(err => {
      message.error(err.message)
    })
  } else {
    try {
      await toClipboard(text)
      message.success('复制成功')
    } catch (e) {
      message.error(e.message)
    }
  }
}

const pagination = reactive({
  page: 1,
  pageSize: 10,
  showSizePicker: true,
  pageSizes: [10, 20, 50, 100],
  onUpdatePage: (page) => {
    pagination.page = page
    getResourceData()
  },
  onUpdatePageSize: (pageSize) => {
    pagination.pageSize = pageSize
    getResourceData()
  }
})
</script>

<template>
  <div class="query-header">
    <div class="logo-content">
      <img alt="" class="logo" src="@/assets/img/JeesResourceLOGO.png">
    </div>
    <div class="query-resource">
      <n-input v-model:value="queryData.kwd" class="query-resource-input" placeholder="请输入资源名称"
               @update:value="getResourceData"/>
      <n-button class="query-resource-btn" type="primary" @click="getResourceData">
        查询
      </n-button>
    </div>
  </div>
  <n-spin :spinning="data.loading">
    <div class="query-content">
      <div v-if="data.resourceData.length > 0 && !data.loading">
        <n-grid :x-gap="24" :y-gap="16" cols="1" responsive="screen">
          <n-grid-item
              v-for="item in data.resourceData.slice((pagination.page - 1) * pagination.pageSize, pagination.page * pagination.pageSize)"
              :key="item.id">
            <n-card :bordered="false">
              <div class="query-content-item">
                <div class="query-content-img">
                  <img :src="item.cover" alt="" width="100%">
                </div>
                <div class="query-content-text">
                  <div class="query-content-title">
                    <n-gradient-text type="info">{{ item.name }}</n-gradient-text>
                  </div>
                  <div class="query-content-desc">
                    <n-text>
                      <n-ellipsis :tooltip="false" line-clamp="2">{{ item.description }}</n-ellipsis>
                    </n-text>
                  </div>
                  <div class="query-content-category-tag">
                    <n-tag size="small" style="margin-right: 5px" type="success">
                      <template #icon>
                        <n-icon :component="FolderOutlined" size="16"/>
                      </template>
                      {{ item.category.name }}
                    </n-tag>
                    <n-tag v-for="tag in item.tags" :key="tag.id" size="small" style="margin-right: 5px" type="info">
                      <template #icon>
                        <n-icon :component="TagOutlined" size="16"/>
                      </template>
                      {{ tag.name }}
                    </n-tag>
                  </div>
                  <div class="query-content-btn">
                    <n-button secondary size="small" style="margin-right: 5px" type="error" @click="linkTo(item.id)">
                      查看
                    </n-button>
                    <n-button v-if="item.baidu_url" secondary size="small" style="margin-right: 5px" type="info"
                              @click="openUrl(item.baidu_url)"><i
                        class="gcfont gc-baiduyun"></i></n-button>
                    <n-button v-if="item.tianyi_url" secondary size="small" style="margin-right: 5px" type="success"
                              @click="openUrl(item.tianyi_url)"><i
                        class="gcfont gc-tianyiyun"></i></n-button>
                    <n-button v-if="item.aliyun_url" secondary size="small" style="margin-right: 5px" type="warning"
                              @click="openUrl(item.aliyun_url)"><i
                        class="gcfont gc-aliyun"></i></n-button>
                    <n-button v-if="item.active_code" size="small" type="primary"
                              @click="copy(item.active_code)">
                      激活码：{{ item.active_code }}
                    </n-button>
                  </div>
                </div>
              </div>
            </n-card>
          </n-grid-item>
        </n-grid>
      </div>
      <n-grid v-if="data.loading" :x-gap="24" :y-gap="16" cols="1" responsive="screen">
        <n-grid-item v-for="i in 2" :span="1">
          <n-card :bordered="false">
            <div class="query-content-item">
              <div class="query-content-img">
                <n-skeleton style="width: 100%;height: 100%"/>
              </div>
              <div class="query-content-text">
                <n-skeleton style="height: 30px;width: 150px;margin-bottom: 12px" text/>
                <n-skeleton :repeat="2" text/>
                <n-skeleton style="width: 250px" text/>
                <n-skeleton style="height: 30px;width: 400px;margin-top: 12px" text/>
              </div>
            </div>
          </n-card>
        </n-grid-item>
      </n-grid>
      <div v-if="data.resourceData.length === 0 && !data.loading">
        <n-empty style="height: 180px;padding-top: 100px">
          <template #icon>
            <n-icon :component="FolderOutlined"/>
          </template>
          <template #default>
            没有找到资源哦
          </template>
        </n-empty>
      </div>
    </div>
  </n-spin>
  <div class="query-pagination">
    <n-pagination v-model:page="pagination.page" v-model:page-size="pagination.pageSize"
                  :item-count="data.resourceData.length" :page-sizes="pagination.pageSizes"
                  :show-size-picker="pagination.showSizePicker" :show-total="true"
                  @update-page-size="pagination.onUpdatePageSize" @update:page="pagination.onUpdatePage"/>

  </div>
  <div class="footer-content">
    <div class="footer-logo">
      <img alt="" src="@/assets/img/JeesResourceLOGO.png">
    </div>
    <div class="footer-copyright">
      <span>Copytight &copy; 2024 小技资源网 All rights reserved.</span>
    </div>
    <div class="footer-content-security">
      <n-a class="footer-content-security-item" href="https://beian.miit.gov.cn/" target="_blank">
        <n-avatar :size="15" alt="" src="/uploads/icp.png" style="margin-right: 5px"/>
        <span>浙ICP备2023003219号-1</span>
      </n-a>
      <n-a class="footer-content-security-item"
           href="https://www.beian.gov.cn/portal/registerSystemInfo?recordcode=33010602000001"
           target="_blank">
        <n-avatar :size="15" alt="" src="/uploads/security.png" style="margin-right: 5px"/>
        <span>浙公网安备 33010602000001号</span>
      </n-a>
    </div>
  </div>
</template>

<style scoped>
.logo-content {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 24px;
}

.logo {
  height: 80px;
}

.query-resource {
  display: flex;
  justify-content: center;
  padding: 24px;
}

.query-resource-input {
  width: 90%;
  margin-right: 1%
}

.query-resource-btn {
  width: 9%
}

.query-content {
  padding: 0 24px;
}

.query-content-item {
  display: flex;
}

.query-content-img {
  width: 35%;
  height: 160px;
}

.query-content-img img {
  height: 100%;
  object-fit: cover;
}

.query-content-text {
  width: 65%;
  padding: 0 24px;
}

.query-content-title {
  font-size: 18px;
}

.query-content-desc {
  font-size: 14px;
  color: #999;
  margin-top: 12px;
}

.query-content-category-tag {
  margin-top: 10px;
}

.query-content-btn {
  margin-top: 10px;
  display: flex;
  flex-wrap: wrap;
}

.query-pagination {
  padding: 24px;
}

.footer-content {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 24px;
  flex-direction: column;
}

.footer-logo {
  height: 25px;
  padding: 10px;
}

.footer-logo img {
  width: 100%;
  height: 100%;
}

.footer-content-security {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px;
}

.footer-content-security-item {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 10px;
  font-size: 12px;
}

@media screen and (max-width: 768px) {
  .query-content-item {
    flex-direction: column;
  }

  .query-content-img {
    width: 100%;
    height: 200px;
  }

  .query-content-text {
    width: 100%;
    margin-top: 10px;
    padding: 0;
  }

  .query-resource-input {
    width: 80%;
  }

  .query-resource-btn {
    width: 19%;
  }
}
</style>